/**
 * @fileoverview CIScrollHelper
 * @author: burning <www.cafeinit.com>
 * @version: 2017-01-16
 */

function CIScrollHelper(opt) {
  this.isListenScrollToStart = false; // 是否开启监听——滑动到顶部
  this.isListenScrollToEnd = true; // 是否开启监听——滑动到低部
  this.leadStart = 25; // 相差顶部多少时触发事件
  this.leadEnd = 100; // 相差底部多少时触发事件
  this.init(opt);
}

module.exports = CIScrollHelper;

var prototype = CIScrollHelper.prototype;

prototype.init = function (opt) {
  opt = opt || {};
  this.$container = opt.container;
  this.$content = opt.content;
  this.leadEnd = opt.leadEnd || this.leadEnd;
  this.willScrollToStartHandler = opt.willScrollToStartHandler;
  this.willScrollToEndHandler = opt.willScrollToEndHandler;

  this._bindEvent();
};

prototype._bindEvent = function () {
  var _this = this;

  this.$container.on('scroll', function (evt) {
    _this.scrollTop = _this.$container.scrollTop();
    _this.containerHeight = _this.$container.height();
    _this.contentHeight = _this.$content.height();

    if (_this.scrollTop <= _this.leadStart) {
      if (_this.isListenScrollToStart) {
        // console.log('willScrollToStart')
        _this.isListenScrollToStart = false;

        if (typeof _this.willScrollToStartHandler === 'function') {
          _this.willScrollToStartHandler();
        }
      }
    } else {
      _this.isListenScrollToStart = true;
    }

    var delta = _this.contentHeight - _this.scrollTop - _this.containerHeight;
    // console.log('CIScrollHelper.scroll', this.scrollTop, delta)

    if (delta <= _this.leadEnd) {
      if (_this.isListenScrollToEnd) {
        _this.isListenScrollToEnd = false;
        if (typeof _this.willScrollToEndHandler === 'function') {
          // 请在 willScrollToEndHandler 中控制 isListenScrollToEnd
          _this.willScrollToEndHandler();
        }
      }
    } else {
      _this.isListenScrollToEnd = true;
    }
  });
};

prototype.scrollTo = function (x, y) {
  this.$container.scrollLeft(x);
  this.$container.scrollTop(y);
};

